import React from "react";
import "./Home.css";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUsers, faIdCardAlt } from "@fortawesome/free-solid-svg-icons";

export const Home = () => {
  return (
    <div>
      <section className="SectionFirst">
        <div className="container-quote">
          <h1 className="mainQuote">Our focus is the client.</h1>
          <Link className="buttom" to="/about">
            Who we are.
          </Link>
          <Link className="buttom" to="/about">
            What we do.
          </Link>
        </div>
      </section>

      <section className="cardsContainer">
        <div className="containerHeaderCard">
          <h1>Mundostar</h1>
        </div>
        <div className="cards">
          <div className="card">
            <FontAwesomeIcon className="ih" icon={faUsers} />
            <h1 className="cardHeader">About us</h1>
            <p className="cardPara">
              Mundotsar was founded in 1995 in order to process different
              offshores operations related to finance and banking.
            </p>
          </div>
          <div className="card">
            <FontAwesomeIcon className="ih" icon={faUsers} />
            <h1 className="cardHeader">Our Team</h1>
            <p className="cardPara">
              Our team is fully committed to attend your need whenever you
              require.
            </p>
          </div>
          <div className="card">
            <FontAwesomeIcon className="ih" icon={faIdCardAlt} />
            <h1 className="cardHeader">Contact</h1>
            <p className="cardPara">
              Mundotsar was founded in 1995 in order to process different
              operations
            </p>
          </div>
        </div>
      </section>
    </div>
  );
};
