import React from "react";
import "./Footer.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelopeSquare, faPhone } from "@fortawesome/free-solid-svg-icons";
import { faLinkedin } from "@fortawesome/free-brands-svg-icons";
import { Link } from "react-router-dom";

const Footer = () => {
  return (
    <footer>
      <div className="copyRight">
        <p>Mundostar 2021 &copy;</p>
      </div>
      <div className="iconsss">
        <p>Contact us:</p>
        <Link to="/contact">
          <FontAwesomeIcon className="if" icon={faEnvelopeSquare} />
        </Link>

        <Link to="/contact">
          <FontAwesomeIcon className="if" icon={faPhone} />
        </Link>
        <Link to="/contact">
          <FontAwesomeIcon className="if" icon={faLinkedin} />
        </Link>
      </div>
    </footer>
  );
};

export default Footer;
